import { CustomEventsHelper } from "../../blazor/custom-events-helper";
import { DxTagNames } from "../../dx-tag-names";
export class ActiveTabChangedEvent extends Event {
    constructor() {
        super(ActiveTabChangedEvent.eventName, {
            bubbles: true,
            composed: false,
            cancelable: true
        });
    }
}
ActiveTabChangedEvent.eventName = DxTagNames.Tabs + ".active-tab-changed";
export class DxTabReorderEvent extends CustomEvent {
    constructor(fromIndex, toIndex) {
        super(DxTabReorderEvent.eventName, {
            detail: new OnTabReorderContext(fromIndex, toIndex),
            bubbles: true,
            composed: true,
            cancelable: true
        });
    }
}
DxTabReorderEvent.eventName = DxTagNames.Tabs + ".tab-reorder";
export class OnTabReorderContext {
    constructor(fromIndex, toIndex) {
        this.fromIndex = fromIndex;
        this.toIndex = toIndex;
    }
}
CustomEventsHelper.register(DxTabReorderEvent.eventName, x => x.detail);
